<template>
    <div class="authorinfo">
        <div class="authorsummay">
            <div class="topbar">作者</div>
            <router-link :to="{name:'user_info',params:{name:userinfo.loginname}}">
                <img :src="userinfo.avatar_url">
            </router-link>
            <router-link :to="{name:'user_info',params:{name:userinfo.loginname}}" class="userNameA">
                <span class="userName">{{userinfo.loginname}}</span>
            </router-link>
        </div>
        <div class="recent_topics">
            <div class="topbar">作者最近主题</div>
            <ul>
                <li v-for="list in topicLimitBy5">
                    <router-link :to="{name:'post_content',params:{id:list.id,name:list.author.loginname}}">
                        {{list.title}}
                    </router-link>
                </li>
            </ul>
        </div>
        <div class="recent_replies">
            <div class="topbar">作者最近回复</div>
            <ul>
                <li v-for="list in replyLimitBy5">
                    <router-link :to="{name:'post_content',params:{id:list.id,name:list.author.loginname}}">
                        {{list.title}}
                    </router-link>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
  name: "SlideBar",
  data() {
    return {
      userinfo: {}
    };
  },
  methods: {
    getData() {
      this.$http
        .get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
        .then(res => {
            console.log(res)
          this.userinfo = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  computed: {
    topicLimitBy5() {
      if (this.userinfo.recent_topics) {
        return this.userinfo.recent_topics.slice(0, 5);
      } else {
        return this.userinfo.recent_topics;
      }
    },
    replyLimitBy5() {
      if (this.userinfo.recent_replies) {
        return this.userinfo.recent_replies.slice(0, 5);
      } else {
        return this.userinfo.recent_replies;
      }
    }
  },
  beforeMount() {
    this.getData();
  }
};
</script>
<style scoped>
.authersummay,
.recent_replies,
.recent_topics {
    background-color: #fff;
}
.authorinfo {
    width: 328px;
    float: right;
    margin-top: 0;
}
li {
    padding: 3px 0;
}   
.recent_replies ul,
.recent_topics ul {
    margin-top: 0px;
    margin-bottom: 0px;
    list-style: none;
    padding-left: 14px;
}

ul a {
    font-size: 12px;
    text-decoration: none;
    color: #778087;
}

.topbar {
    padding: 10px;
    background-color: #f6f6f6;
    height: 16px;
    font-size: 12px;
    margin-top: 10px;
}

img {
    height: 48px;
    width: 48px;
    border-radius: 3px;
    margin: 10px;
}
.userNameA{
    text-decoration: none;
    color: #778087;
}
.userName{
    margin-top: 20px;
    position: absolute;
}
.loginname {
    width: 100px;
    float: right;
    margin-top: 22px;
    margin-right: 159px;
    font-size: 14px;
}

.loginname a {
    text-decoration: none;
    color: #778087;
}
.authorsummay{
    background: #fff;
    border-radius: 5px;
    position: relative;

}
.authorsummay .topbar {
    margin-top: 0px;
}
</style>



